.payments-container {
    padding: 0 20rem 2rem 20rem;
    margin: 0 0 2rem 0;
    text-align: center;
}
.payments-container h2 {
    font-size: 20px;
    font-weight: 600;

}
.payments-container h3 {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 2rem;
}

.payments-container .payment-type h2{
    font-size: 16px;
    font-weight: 300;
    margin: 2rem 0 0 0 ; padding: 0.5rem 0.5rem;
    color: rgb(103, 165, 147);
    border: 2px solid rgb(116, 175, 139);
}
.payments-container .form {
    display: flex; flex-direction: column; justify-content: center;
    padding: 0 2rem;
    margin: auto;
    margin-top: 2rem;
}
.payments-container .form input {
    text-align: center;
    margin-bottom: 1rem;
}
.payments-container .form h4 {
    font-size: 16px;
    margin: 0;
}

@media only screen and (max-width: 780px) {
    .payments-container {
        padding: 0 1rem;
    }
    .payments-container .form {
        padding: 0;
    }
}