.c-ayudar-view {
    width: 100%; height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.title {
    align-items: center;
    justify-content: center;
}

.donation {
    text-align: center;
    width: 100%; height: 100%;
    padding: 2rem 25%;
    margin: 2rem 0;
    border: solid 2px rgb(109, 168, 135);
}
.donation h2 {
    font-size: 28px;
    margin-bottom: 2rem;
}
.donation a {
    font-size: 18px;
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
    border-radius: 0.2rem;
    background-color: rgb(72, 158, 122);
}

.donation-container {
    width: 100%; height: 100%;
    display: flex; flex-direction: column;
    align-items: center;
}
.donation-header {
    width: 100%; height: 100%;
    padding: 2rem 0;
    margin: 2rem 0 2rem 0   ;
    display: flex; flex-direction: column;
    align-items: center;
    text-align: center;
}
.donation-header h2 {
    margin-bottom: 1rem;
    padding: 0 25%;
}
.donation-header .donation-buttons {
    display: flex; align-items: center; justify-content: center;
}
.donation-header .donation-buttons a {
    padding: 0.5rem 0.5rem;
    margin: 0 0.5rem;
    background-color: rgb(84, 170, 127);
    color: white;
    font-size: 18px;
    border-radius: 0.2rem;
    cursor: pointer;
}


 form {
    width: 50%;
    text-align: center;
}
form .form-level {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}
form .form-level input {
    text-align: center;

}
form .form-level textarea {
    text-align: center;
} 


@media only screen and (max-width: 780px) {
    .donation {
        padding: 2rem 10%;
    }
    .donation-header h2 {
        padding: 0 2rem;
    }


    .donation-header .donation-buttons {
        flex-direction: column;
        width: 100%;
    }
    .donation-header .donation-buttons a {
        width: 90%;
        margin-bottom: 1rem;
    }

    form {
        width: 66%;
    }
}